import titleComponent from './titleComponent.vue';
import listItem from './listItemComponent.vue';
import {getMyHistoryList} from "../../api/people/api.js"
export default {
	components: {
		listItem: listItem,
		titleComponent: titleComponent
	},
	data() {
		return {
			selectID: '',
			modalShow: false,
			isShownBack: true,
			userInfo: {
				userId: '',
				name: '',
				avatar: '',
			},
			title: '我的足迹',
			// list的数据
			// type: 0->文字   1->语音    2->视频
			list: [],
		}
	},

	methods: {
		// 这里写退出
		back() {
			uni.switchTab({
				url: '/pages/people/people_main',
			});
		},
		// 跳转查看详情
		showDetail(itemID) {
			uni.navigateTo({
				url: `../story/storyDetails?sid=${itemID}`, 
			});
		}
	},
	onLoad() {
		
	},
	onShow() {
		getMyHistoryList().then((res)=>{
			const _self = this
			if (res.data) {
				console.log(res.data, "data")
				const temp = JSON.parse(JSON.stringify(res.data.data));
				console.log("temp",temp)
				_self.list = [];
				for (var i = 0; i < temp.length; i++) {
					// const name_may_miss = "匿名用户";
					// const avatar_may_miss = "../../static/avatar_miss.jpg";
					// if(temp[i].isShow){
					// 	name_may_miss = ;
					// 	avatar_may_miss = ;
					// }
					_self.list.push({
						sid: temp[i].sid,
						name: temp[i].username,
						time: temp[i].pubDate,
						place: temp[i].location_name,
						avatar: temp[i].avatar,
						desc: temp[i].desc
					})
				}
			} else {
				uni.showToast({
					icon:'loading',
					title:'获取数据失败'
				})
			}
		}).catch((err)=>{
			console.log(err)
		})
		
	},
}
